/* Home.module.css*/

.home {
    display: flex;
    justify-content: space-between;
    align-items: center;
    overflow: hidden;
    line-height: 1;
    height: calc(100vh - 6rem);
    
}

.home-content {
    margin-top: 6rem;
    margin-bottom: 30px;
}
.home-title {
    align-items: start;                                                   
}

.home-title h2 {
    margin-bottom: 1rem;                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                             
    transform: translateY(500px);
    animation: slideUp 0.8s ease forwards 0.5s;
}
     
.home-title h1 {
    font-size: 5rem;
    font-weight: 700;
    transform: translateY(500px);
    animation: slideUp 0.8s ease forwards 0.7s;
    font-family: Mulish, sans-serif;

}

.text-wrapper {
    display: flex;
    align-items: center;
    font-size: 1.5rem;
    transform: translateY(500px);
    animation: slideUp 0.8s ease forwards 0.9s;
}
.text-wrapper p{
  width: 370px;
  font-weight: 700;
}

.items {
    font-size: 3rem;
    opacity: 1;
    animation: fadeInOut 3s ease-in-out infinite;
}

.icons{
    display: flex;
    justify-content: start;
    align-items: center;
    margin-top: 20px;
    font-size: 1.2rem;
    font-weight: 700;
    transform: translateY(500px);
    animation: slideUp 0.8s ease forwards 1.1s;
}
.iconLink {
    margin-left: 10px; /* Adjust the margin as needed */
  }
  
  .iconLink a {
    color: #000; /* Adjust the color as needed */
    font-size: 1.5rem; /* Adjust the font size as needed */
    transition: color 0.6s;
  }
  
  .iconLink a:hover {
    color: #444;
  }
  .iconLabel{
    font-size: 1rem;
}
.links {
    display: flex;
    justify-content: start;
    align-items: center;
    margin-top: 20px;
    font-size: 1.2rem;
    font-weight: 700;
    transform: translateY(500px);
    animation: slideUp 0.8s ease forwards 1.1s;
}

.link {
    position: relative;
    padding-bottom: 2px;
}

.link:hover {
    color: #000;
    transition: color 0.6s;
}

.link a::after {
    content: "";
    position: absolute;
    bottom: -5px;
    left: 0;
    width: 100%;
    border: 1.8px solid #000;
    transform: scaleX(0);
    transition: transform 0.6s cubic-bezier(0.19, 1, 0.22, 1);
    transform-origin: right center;
}

.link a:hover::after {
    transform: scaleX(1);
    transform-origin: left center;
    transition-duration: 0.4s;
}

.divider{
    font-size: 1.5rem;
    padding: 0 15px;
}
.scroll{
    position: relative; 
    transform: translateY(500px);
    animation: slideUp 0.8s ease forwards 1.3s;
}
.scroll a span{
    position: absolute;
    bottom: -70px;
    left: 16px;
    width: 30px;
    height: 50px;
    margin-left: -15px;
    border: 2px solid #000;
    border-radius: 50px;
    box-sizing: border-box;
}


.scroll a span:hover{
    border: 2px solid #444;
    transition: all 0.4s;
}

.scroll a span::before{

    content: "";
    position: absolute;
    top: 5px;
    left: 45%;
    width: 2px;
    height: 15px;
    background-color: #000;
    border: 2px solid #000;
    border-radius: 30%;
    animation: scroll ease 2s infinite;
    box-sizing: border-box;
}

.scroll a span:hover::before{
    background-color: #444;
    transition: all 0.4s;
}

.circle{
    position: relative;
    width: 450px;
    height: 450px;
    background-color: #999;
    border-radius: 50%;
    overflow: hidden;
}

.circle::before {
    content: "";
    position: absolute;
    inset: -10px 100px;
    background: linear-gradient(#000, #000);
    transition: 0.5s;
    animation: animationCircle 4s linear infinite;
}

.circle::after{
    content: "";
    position: absolute;
    inset: 10px;
    background: #fff;
    border-radius: 50%;
    z-index: 1;
}

.content{
    position: absolute;
    inset: 30px;
    border: 6px solid #000;
    z-index: 3;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    flex-direction: column;
}

.content img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: 0.5s;
    text-align: center;
    pointer-events: none;
    z-index: 3;
}

.content img:hover{
    transform: scale(1.1);
    transition: 0.5s;
}

.content h2{
    position: relative;
    text-align: center;
    font-size: 15px;
    font-weight: 700;
    letter-spacing: 2px;
    text-transform: uppercase;
    padding: 10px 0;
}
.content h2 span{
    display: block;
    font-weight: 300;
    font-size: 13px;
    padding: 5px 0;
}

.content a{
    position: relative;
    background-color: #222;
    color: #fff;
    padding: 10px 20px;
    font-size: 14px;
    font-weight: 600;
    border-radius: 20px;
    text-transform: uppercase;
    letter-spacing: 1px;
    transition: 0.5s;
}

.content a:hover{
    letter-spacing: 3px;
}

.circle:hover::before{
    inset: -20px 0px;
}

.circle:hover img{
    opacity: 0;
}


.skills {
    display: flex;
    font-size: 1.7rem;
    display: flex;
    margin-top: 40px;
    left: 0;
}

.skills p {
    font-size: 1.2rem;
    font-weight: 700;
    border-right: 2px solid rgba(45,46,50,.5);
    color: #2d2e32;
    font-weight: 600;
    padding-right: 2rem;
    display: block;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    
  }
  
  .logos ul {
    list-style: none;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    
  }

  .logos li {
    margin: 0 14px;
    
  }
@keyframes slideUp {
    0% {
        transform: translateY(500px);
    }
    100% {
        transform: translateY(0px);
    }
}

@keyframes fadeInOut {
    0% {
        opacity: 0;
        transform: scale(0.8);
    }

    30% {
        opacity: 1;
        transform: scale(1.2);
    }

    70% {
        opacity: 1;
        transform: scale(1.2);
    }

    100% {
        opacity: 0;
        transform: scale(0.8);
    }
}

@keyframes scroll {
    0%{
        transform: translateY(0);
    }
    100%{
        transform: translateY(10px);
    }
}

@keyframes animationCircle {
    0% {
        transform: rotate(0);
    }
    100% {
        transform: rotate(360deg);
    }
}

@media screen and (max-width: 890px) {
    .home-title h1{
        font-size: 4rem;

    }

    .text-wrapper {
        font-size: 1.8rem;
    }
    .items{
        font-size: 2.2rem;
    }

    .links{
        font-size: 1rem;
        margin-top: 10px;
        align-items: center;
    }

    .divider{
        font-size: 1.2rem;
    }
    .circle{
        width: 340px;
        height: 240px;
    }
    .skills{
        flex-direction: column;
        align-items: center;
    }
    .skills p{
        border-bottom: 2px solid rgba(45,46,50,.5);
        border-right: none;
        padding-right: 0rem;
        margin-bottom: 2rem;
        padding-bottom: 1rem;

    }
}

@media screen and (max-width: 768px){
    .home{
        flex-direction: column-reverse;
        justify-content: space-around;
        line-height: 0.8;
        align-items: center;
        height: auto;
    }
    .home-content{
        margin-top: 20px;
        align-items: center;
        text-align: center;

    }

    .text-wrapper p{
        width: 390px;
        margin-top: 3rem;
        text-align: center;
    }
    .circle{
        width: 350px;
        height: 350px;

    }
    .links {
        text-align: center;
        display: flex;
        justify-content: center;
    }
    .links a{
        margin-right: 10px;
        margin-left: 8px;
    }
    .icons{
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 20px;
        font-size: 1rem;
        animation: slideUp 0.8s ease forwards 1.1s;
    }

    .iconLabel{
        font-size: 1rem;
    }
    .iconLink a {
        font-size: 1.4rem;
    }
}